import React, { useState } from 'react'
import './Authent.scss'

function Authent(props) {
  const [login, setLogin] = useState('')
  const [password, setPassword] = useState('')

  const [wrongData, setWrongData] = useState(false)
  const [nullData, setNunllData] = useState(false)

  const auth = {username: login, password: password}
  let url = '5.187.6.228' 
  
  // function enterLogin(event) {
  //   alert('ss')
  //   if (event.keyCode === 13) {
  //       alert('5');
  //   }
  // }
  window.onkeydown = enterLogin

  function enterLogin(e) {
    if (e.keyCode === 13) {
      message()
    }
  }

  function message(){
    setWrongData(false)
    setNunllData(false)
    if(login === '' || password === ''){
      setNunllData(true)
    }else{
      fetch(`http://`+url+`/auth/`, {
        method: 'POST',
        headers: {'Content-Type': 'application/json'},
        body: JSON.stringify(auth)
      })
      .then( data => data.json())
      .then(
        data => {
          console.log(data);
          if (typeof(Storage) !== "undefined") {
            localStorage.setItem("userData", JSON.stringify(data));
          }
          if(data.non_field_errors === undefined){
            window.location.href = window.location.origin + '/'
          }else{
            setWrongData(true)
            localStorage.removeItem("userData")
          }
        }
      ).catch( error => console.log(error))
    }
  }
  return(
    <div className="login_page">
      <div className="login">
        <h1>Sign in</h1>
        <div style={{paddingBottom: wrongData === true || nullData === true ? '48%' : '35%' }}>
          <label>login</label>
          <input onChange={event => setLogin(event.target.value)} placeholder="Enter login"></input>
          <label>password</label>
          <input type="password" onChange={event => setPassword(event.target.value)} placeholder="Enter password"></input>
          {wrongData?
            <label style={{ color: 'red', padding: '2%'}}>Wrong password or login</label>
          : null }
          {nullData?
            <label style={{ color: 'red', padding: '2%'}}>Login or Password is empty</label>
          : null }
          <button id="login" onClick={message}>Sign in</button>
        </div>
      </div> 
    </div>
  )
}

export default Authent;