import Sidebar from "./Components/Sidebar.js";
import Overview from "./Components/Overview.js";
import Municipal from "./Components/Municipal.js";
import Monitoring from "./Components/Monitoring.js";
import Authent from "./Components/Authent.js";

import './App.scss';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';

function App() {
  return (
    <div className="App">
      <Router>
      {localStorage.getItem("userData") ?
        <Sidebar/>
      :null}
        <Switch>
          {localStorage.getItem("userData") ?
            <Route path={localStorage.getItem("userData") ? '/muni' : '/overview/'} exact component={Overview}/>
          :null}
          {localStorage.getItem("userData") ?
            <Route path="/municipal/"  exact component={Municipal}/>
          : null }
          {localStorage.getItem("userData") ?
            <Route path="/monitoring/" exact component={Monitoring}/>
          : null }          
          <Route path={localStorage.getItem("userData") ? '' : '/muni'} exact component={Authent}/>
        </Switch>
      </Router>
    </div>
  );
}

export default App;
