import React from 'react';
import './Filters.scss'

function Filters({setDatefilterStart, datefilterStart, setDatefilterStop, datefilterStop}) {
  return(
    <div className="filter">
      <input type="date" max={datefilterStop} onChange={event => setDatefilterStart(event.target.value)} value={datefilterStart} />
      <input type="date" min={datefilterStart} onChange={event => setDatefilterStop(event.target.value)} value={datefilterStop} />
    </div>
  )
}

export default Filters;