import React, { useState, useEffect } from 'react';
import './Overview.scss';
import Profile from "./Profile.js";
import BigScore from "./BigScore.js";
import Score from "./Score.js";
import Belgrad from "./Belgrad.js";
import TelAviv from "./TelAviv.js";

import hand from './../Static/hand.svg'
import education from './../Static/education.svg'
import person from './../Static/person.svg'
import phone from './../Static/phone.svg'
import justice from './../Static/justice.svg'
import right from './../Static/right.svg'

import ReactWordcloud from 'react-wordcloud';

function Overview() {
  let [score, setScores] = useState()
  let [tranding, setTrandings] = useState()
  let [modalwindow, setModalwindows] = useState()

  let filtersValue = true
  let [datefilterStart, setDatefilterStart] = useState('')
  let [datefilterStop, setDatefilterStop] = useState('')

  let [modalState, setModalState] = useState(true)
  let [districtNum, setDistrictNum] = useState(99)

  let [url] = useState('5.187.6.228:8000') 

  let [userData] = useState(JSON.parse(localStorage.getItem("userData")))

  useEffect(() => {
    fetch(`http://`+url+`/api/overview_score/?start_date=`+datefilterStart.split('-').reverse().join('-')+`&end_date=`+datefilterStop.split('-').reverse().join('-'), {
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Token ${userData.token}`
      },
    })
    .then( data => data.json())
    .then( data => {setScores(data)} )
    .catch( error => console.error(error))

    fetch(`http://`+url+`/api/overview_treding/?start_date=`+datefilterStart.split('-').reverse().join('-')+`&end_date=`+datefilterStop.split('-').reverse().join('-'), {
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Token ${userData.token}`
      },
    })
    .then( data => data.json())
    .then( data => {setTrandings(data)} )
    .catch( error => console.error(error))

    fetch(`http://`+url+`/api/overview_modalwindow/?start_date=`+datefilterStart.split('-').reverse().join('-')+`&end_date=`+datefilterStop.split('-').reverse().join('-'), {
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Token ${userData.token}`
      },
    })
    .then( data => data.json())
    .then( data => {setModalwindows(data)} )
    .catch( error => console.error(error))
  }, [url, userData.token, datefilterStart, datefilterStop])
  
  console.log(modalwindow)
  let mapValues

  if(userData.role===1){
    mapValues = [
      {fill: '#6CE4A0', stroke: 'grey', subdivision: 'Stari Grad', city_score: '1'},
      {fill: '#D86874', stroke: 'grey', subdivision: 'Savski Venac', city_score: '4'},
      {fill: '#B3DF75', stroke: 'grey', subdivision: 'Vračar', city_score: '10'},
      {fill: '#F3DA50', stroke: 'grey', subdivision: 'Novi Beograd', city_score: '1'},
      {fill: '#6CE4A0', stroke: 'grey', subdivision: 'Čukarica', city_score: '7'},
      {fill: '#EEB359', stroke: 'grey', subdivision: 'Rakovica', city_score: '1'},
      {fill: '#6CE4A0', stroke: 'grey', subdivision: 'Voždovac', city_score: '9'},
      {fill: '#6CE4A0', stroke: 'grey', subdivision: 'Zvezdara', city_score: '8'},
      {fill: '#F3DA50', stroke: 'grey', subdivision: 'Zemun', city_score: '1'},
      {fill: '#F3DA50', stroke: 'grey', subdivision: 'Palilula', city_score: '10'},
      {fill: '#6CE4A0', stroke: 'grey', subdivision: 'Surčin', city_score: '6'},
      {fill: '#D86874', stroke: 'grey', subdivision: 'Obrenovac', city_score: '9'},
      {fill: '#F3DA50', stroke: 'grey', subdivision: 'Barajevo', city_score: '5'},
      {fill: '#EEB359', stroke: 'grey', subdivision: 'Sopot', city_score: '1'},
      {fill: '#F3DA50', stroke: 'grey', subdivision: 'Grocka', city_score: '3'},
      {fill: '#EEB359', stroke: 'grey', subdivision: 'Lazarevac', city_score: '1'},
      {fill: '#D86874', stroke: 'grey', subdivision: 'Mladenovac', city_score: '50'}
    ] 
  }else{
    mapValues = [
      {fill: '#6CE4A0', stroke: 'grey', subdivision: 'Ayalon Hway', city_score: '1'},
      {fill: '#D86874', stroke: 'grey', subdivision: 'Ibn Grivol', city_score: '4'},
      {fill: '#B3DF75', stroke: 'grey', subdivision: 'Eilat', city_score: '10'},
      {fill: '#F3DA50', stroke: 'grey', subdivision: 'Sderot Rothschild', city_score: '1'},
      {fill: '#6CE4A0', stroke: 'grey', subdivision: 'Bograshov', city_score: '7'},
      {fill: '#EEB359', stroke: 'grey', subdivision: 'HaYarkon', city_score: '1'},
      {fill: '#6CE4A0', stroke: 'grey', subdivision: 'Yarkon River', city_score: '9'},
      {fill: '#6CE4A0', stroke: 'grey', subdivision: 'HaRakevet', city_score: '8'},
      {fill: '#F3DA50', stroke: 'grey', subdivision: 'Shaul HaMelech', city_score: '1'}
    ]     
  }

  const modal = (e) => {
    if(e.target.classList[0] !== 'map_model'  && e.target.parentNode.classList[0] !== 'map_model' && e.target.parentNode.parentNode.classList[0] !== 'map_model' && e.target.parentNode.parentNode.parentNode.classList[0] !== 'map_model' && e.target.parentNode.parentNode.parentNode.parentNode.classList[0] !== 'map_model'){
      if(e.target.attributes.value !== undefined){
        if(districtNum !== Number(e.target.attributes.value.nodeValue) && modalState === false){
          setModalState(false)
          setDistrictNum(Number(e.target.attributes.value.nodeValue))          
        }else if(modalState === true){
          setModalState(false)
          setDistrictNum(Number(e.target.attributes.value.nodeValue))
        }
        else{
          setModalState(true)
          setDistrictNum(99)
        }
      }else if(modalState === false){
        setModalState(true)
        setDistrictNum(99)
      }
    }
  }

  let TrendKeyWords = []

  let modalWindow = [
    { value: 'Social Class', procent: '', icon: hand},
    { value: 'Education', procent: '', icon: education },
    { value: 'Social Media', procent: '', icon: person },
    { value: '106 Score', procent: '', icon: phone },
    { value: 'Balance Score', procent: '', icon: justice }
  ]

  if(tranding !== undefined && tranding.detail !== 'You do not have permission to perform this action.'){
    TrendKeyWords = []
    for(let x = 0; x<tranding.length; x++){
      TrendKeyWords.push({value: tranding[x].score, text: tranding[x].value, color: tranding[x].color});
    }
  }
  const callbacks = {
    getWordColor: TrendKeyWords => TrendKeyWords.color === false ? '#28BE8C' : '#E4293F'
  }
  const options = {
    animations: true,
    rotations: false,
    deterministic: true,
    fontFamily: "'Mulish', sans-serif",
    fontSizes: [12, 40],
    padding: 5
  };

  if(modalwindow !== undefined && modalwindow.detail !== 'You do not have permission to perform this action.' && modalwindow.length !== 0){
    
    let fillColor

    for(let x = 0; x<modalwindow.length; x++){
      let index = mapValues.findIndex(p => p.subdivision === modalwindow[x].district_name)

        if(modalwindow[x].score_percent < 20){
          fillColor = '#D86874'
        }else if(modalwindow[x].score_percent < 40){
          fillColor = '#EEB359'
        }else if(modalwindow[x].score_percent < 60){
          fillColor = '#F3DA50'
        }else if(modalwindow[x].score_percent < 80){
          fillColor = '#B3DF75'
        }else{
          fillColor = '#6CE4A0'
        }
        mapValues[index].fill = fillColor
        mapValues[index].city_score = String(modalwindow[x].score_percent).slice(0, 4)
    }
    if(modalwindow[districtNum] !== undefined){
      modalWindow[0].procent = modalwindow[districtNum].social_class_score
      modalWindow[1].procent = modalwindow[districtNum].a_106_score
      modalWindow[2].procent = modalwindow[districtNum].education_score
      modalWindow[3].procent = modalwindow[districtNum].balance_score
      modalWindow[4].procent = modalwindow[districtNum].social_media_score
    }
  }else{
    for(let x=0; x<mapValues.length; x++){
      mapValues[x].fill = '#bbb4'
    }
  }

  const modal_window_div = modalWindow.map((variable, index) => {
    return(
      <div className="stat_elem" key={index}>
        <div>
          <img alt={variable.value} src={variable.icon} />
        </div>
        <div>
          <span>{String(variable.procent).slice(0, 4)}%</span>
          <span>{variable.value}</span>
        </div>
        <div>
          <img alt={variable.value} src={right} />
        </div>
      </div>
  )})

  return (
    <div className="Overview">
      <Profile name={'Overview'} filtersValue={filtersValue} setDatefilterStart={setDatefilterStart} datefilterStart={datefilterStart} setDatefilterStop={setDatefilterStop} datefilterStop={datefilterStop}/>
      <div className="Overview-elements">
        {score ?
          <div className="Rankings">
            <span>Rankings</span>
            <div className="rank_score">
              {(score[0] && score[1]) ?
                <>
                  {(score[1].value).length < (score[0].value).length ?
                    <>
                      <BigScore score={score[1].percent} value={score[1].value} />
                      <BigScore score={score[0].percent} value={score[0].value} />
                    </>
                  :
                    <>
                      <BigScore score={score[0].percent} value={score[0].value} />
                      <BigScore score={score[1].percent} value={score[1].value} />
                    </>
                  }
                </>
              : 
                <>
                  <BigScore score={null} value={null} />
                  <BigScore score={null} value={null} />
                </>  
               }
            </div> 
          </div>
        : null}
        {tranding ?
          <div className="Trending-Topics">
            <span className="topic">Trending Topics</span>
            <div className="kaysDiv" style={{height: '270px'}}>
              <ReactWordcloud callbacks={callbacks} options={options} words={TrendKeyWords} />
            </div>
          </div>
        : null}
      </div>
      {modalwindow ? 
        <div className="map" onClick={modal}>
          <div style={{visibility: (modalState ? 'hidden' : 'visible'), opacity: (modalState ? '0' : '1')}} className="map_model">
            <header>
              <span className="topic">{mapValues[districtNum] !== undefined ? mapValues[districtNum].subdivision: ''}</span>
            </header>
            <div className="score">
              {(mapValues[districtNum] && modalwindow.length !== 0) ? 
                <Score score={mapValues[districtNum].city_score} value={'Area score'} />
              : 
                <Score score={null} value={'Area score'} />
              }
            </div>
            <div className="stats">
              {modal_window_div}
            </div>
          </div>
          <div className="map_itself">
            <div className="svg_map"> 
            {userData.role === 1 ?
              <Belgrad mapColors={mapValues} /> 
              :<TelAviv mapColors={mapValues} />
            }
            </div>
          </div>
        </div>        
      : null}
    </div>
  );
}
  
export default Overview;