import React, { useState, useEffect } from 'react';
import {Line} from 'react-chartjs-2'

import Profile from "./Profile.js";
import Score from "./Score.js";
import TrendingPost from "./Trending_Post.js";
import Filters from "./Filters.js";
import './Monitoring.scss';

import rate from './../Static/rate.svg'
import person from './../Static/person.svg'

import arrow from './../Static/icon/arrow.svg'
import ReactWordcloud from 'react-wordcloud';

function Monitoring() {
  let [datefilterStart, setDatefilterStart] = useState('')
  let [datefilterStop, setDatefilterStop] = useState('')

  let [chartNum, setChartNum] = useState(30)
  let [chartValue, setChartValue] = useState('Last 30 days')

  let [stats, setStats] = useState()
  let [sentimentAnalysis, setSentimentAnalysis] = useState()


  let [trendingKeys, setTrendingKeys] = useState()
  
  let [url] = useState('5.187.6.228:8000') 

  let [userData] = useState(JSON.parse(localStorage.getItem("userData")))
  
  useEffect(() => {
    fetch(`http://`+url+`/api/monitoring_stats/?start_date=`+datefilterStart.split('-').reverse().join('-')+`&end_date=`+datefilterStop.split('-').reverse().join('-'), {
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Token ${userData.token}`
      },
    })
    .then( data => data.json())
    .then( data => { setStats(data) } )
    .catch( error => console.error(error))
  }, [url, userData, datefilterStart, datefilterStop])
  
  useEffect(() => {
    fetch(`http://`+url+`/api/monitoring_get_keywords/`, {
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Token ${userData.token}`
      },
    })
    .then( data => data.json())
    .then( data => { setTrendingKeys(data) } )
    .catch( error => console.error(error))
  }, [url, userData])

  useEffect(() => {
    fetch(`http://`+url+`/api/monitoring_sentimentAnalysis/?days=`+chartNum, {
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Token ${userData.token}`
      },
    })
    .then( data => data.json())
    .then( data => { setSentimentAnalysis(data) } )
    .catch( error => console.error(error))
  }, [url, userData, chartNum])

  let [positiveTrendingPosts, setPositiveTrendingPosts] = useState()
  let [neutralTrendingPosts, setNeutralTrendingPosts] = useState()
  let [negativeTrendingPosts, setNegativeTrendingPosts] = useState()

  let [positivePage, setPositivePage] = useState(0)
  let [neutralPage, setNeutralPage] = useState(0)
  let [negativePage, setNegativePage] = useState(0)

  useEffect(() => {
    fetch(`http://`+url+`/api/monitoring_trendingPosts/?sentiment=positive&limit=10&page=`+positivePage, {
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Token ${userData.token}`
      },
    })
    .then( data => data.json())
    .then( data => { setPositiveTrendingPosts(data) } )
    .catch( error => console.error(error))
  }, [url, userData, positivePage])

  useEffect(() => {
    fetch(`http://`+url+`/api/monitoring_trendingPosts/?sentiment=neutral&limit=10&page=`+neutralPage, {
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Token ${userData.token}`
      },
    })
    .then( data => data.json())
    .then( data => { setNeutralTrendingPosts(data) } )
    .catch( error => console.error(error))
  }, [url, userData, neutralPage])

  useEffect(() => {
    fetch(`http://`+url+`/api/monitoring_trendingPosts/?sentiment=negative&limit=10&page=`+negativePage, {
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Token ${userData.token}`
      },
    })
    .then( data => data.json())
    .then( data => { setNegativeTrendingPosts(data) } )
    .catch( error => console.error(error))
  }, [url, userData, negativePage])

  function formatNumber(num) {
    if(num !== null && num !== undefined){
      return num.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1,')
    }else{
      return num
    }
  }

  //Названия элементов в блоке SM Stats
  const rankings_stats = [
    { value: 'Engagemnt Rate', procent: '', icon: rate},
    { value: 'New Followers',  procent: '', icon: person}
  ]
  
  // Слова в блоке Trending Key Words
  const TrendKeyWords = []
  
  if(stats !== undefined && stats.detail !== 'Invalid token.'){
    if(stats[0].engagemnt_rate !== null){
      rankings_stats[0].procent = String(stats[0].engagemnt_rate).slice(0, 4)
    }else{
      rankings_stats[0].procent = 'null '
    }
    if(stats[0].followers !== null){
      rankings_stats[1].procent =  formatNumber((stats[0].followers).toFixed(2))
    }else{
      rankings_stats[1].procent = 'null '
    }
  }

  let dates = []
  let chartArr = []
  let data = {
    labels: dates,
    datasets: [],
  };

  if(sentimentAnalysis !== undefined && sentimentAnalysis.detail !== 'Invalid token.'){
    for(let x=0; x<sentimentAnalysis.length; x++){
      if(!dates.includes(sentimentAnalysis[x].date)){
        dates.push(sentimentAnalysis[x].date)
      }
      let pos='pos_'
      if(sentimentAnalysis[x].sentiment === 'negative'){
        pos='neg_'
      }
      if(chartArr.length === 0){
          chartArr.push({label: pos + sentimentAnalysis[x].keyword, sentiment: sentimentAnalysis[x].sentiment, mention: [] })
      }else{
        if(!chartArr.some(e => e.label === pos + sentimentAnalysis[x].keyword)) {
          chartArr.push({label: pos + sentimentAnalysis[x].keyword, sentiment: sentimentAnalysis[x].sentiment, mention: [] })
        }
      }
      let index = chartArr.findIndex(p => p.label === pos + sentimentAnalysis[x].keyword)
      chartArr[index].mention.push(sentimentAnalysis[x].mention)
    }
    for(let x=0; x<chartArr.length; x++){
      let border = 'green'
      if(chartArr[x].sentiment === 'negative'){
        border = 'red'
      }
      data.datasets.push({label: chartArr[x].label, data: chartArr[x].mention, backgroundColor: border, borderColor: border})
    }
  }

  const optionsKeys = {
    rotations: false,
    fontFamily: "'Mulish', sans-serif",
    padding: 10,
    fontSizes: [16, 90],
    colors: 'black'
  };

  if(trendingKeys !== undefined && trendingKeys.detail !== 'Invalid token.'){
    for(let x=0; x<trendingKeys.length; x++){
      TrendKeyWords.push({value: trendingKeys[x].nentry, text: trendingKeys[x].word})
    }
  }

  const rankings = rankings_stats.map((elem, index) => {
    return(
      <div className="rank_elem" key={index}>
        <div className="round_img">
          <img src={elem.icon} alt={elem.value} />
        </div>
        <div className="data">
          <span>{elem.procent}{index === 0 ? '%' : null}</span>
          <span>{elem.value}</span>
        </div>
      </div>
    )
  })

  function monthDropFilter(){
    if (chartNum === 30){
      setChartValue(chartValue = 'Last 60 days')
      setChartNum(chartNum = 60)
    }else{
      setChartValue(chartValue = 'Last 30 days')
      setChartNum(chartNum = 30)
    }
  }

  function dropMenu(){
    let drop = document.querySelector('.drop')
    if (drop.style.display === 'block'){
      drop.style.display = 'none'
    }else{
      drop.style.display = 'block'
    }
  }
  
  return(
    <div className="monitoring">  
      <Profile name={'SM Monitoring'} />
      { stats ?
        <div id="stats" className="stats">
          <h1>SM Stats</h1>
          <div className="rankings">
            <div className="rank_header">
              <span className="topic">Rankings</span>
              <div className="filters">            
                <Filters setDatefilterStart={setDatefilterStart} datefilterStart={datefilterStart} setDatefilterStop={setDatefilterStop} datefilterStop={datefilterStop}/>
              </div>
            </div>
            <div className="rankings_content">
              {stats[0] ?
                <Score score={stats[0].city_score} value={'Area score'} />
              : <Score score={null} value={'Area score'} />}
              {rankings}
            </div>
          </div>
        </div>
      : null } 
      { (positiveTrendingPosts || neutralTrendingPosts || negativeTrendingPosts) ?
        <div id="trending" className="trending">
          <h1>Trending Posts</h1>
          <div className="trending_posts">
            {positiveTrendingPosts ?
              <TrendingPost TrendingPosts={positiveTrendingPosts} setPage={setPositivePage} page={positivePage} state={'Positive'} />
            : null }
            {neutralTrendingPosts ?
              <TrendingPost TrendingPosts={neutralTrendingPosts} setPage={setNeutralPage} page={neutralPage} state={'Neutral'} />
            : null }
            {negativeTrendingPosts ?
              <TrendingPost TrendingPosts={negativeTrendingPosts} setPage={setNegativePage} page={negativePage} state={'Negative'} />
            : null }
          </div>
        </div>
      : null }  
      {sentimentAnalysis ?
        <div id="chart" className="chart">
          <h1>Sentiment Analysis</h1>
          <div className="chart_header">
            <span className="topic"></span>
            <div className="filters_div">
              <div className="dropdown">
                <div className="mainDrop" onClick={dropMenu} value="30">
                  <span>{chartValue}</span>   
                  <img src={arrow} alt="arrow" /> 
                </div>
                <div className="drop" onClick={dropMenu}>
                  <span value="2" className="dateFilter" onClick={monthDropFilter}>
                    {chartNum === 60 ?
                      'Last 30 days'
                      : 'Last 60 days'
                    }
                  </span>
                </div>
              </div>
            </div>
          </div>
          <div className="chart_div">
          <Line data={data} 
              options={{
                responsive: true,
                animation: false
              }}
            />
          </div>
        </div>
      : null }
      {trendingKeys ?
        <div id="Trending-Topics" className="Trending-Topics">
          <h1 className="topic">Trending Key Words</h1>
          <div className="keys">
            <ReactWordcloud options={optionsKeys} words={TrendKeyWords} />
          </div>
        </div>
      : null }
    </div>
  );
}

export default Monitoring;
