import React from 'react';
import './Trending_Post.scss'
import twitter_colored from './../Static/icon/icon-logo-twitter.png'
import left from './../Static/icon/left.svg'
import right from './../Static/icon/right.svg'

function Trending_Post({TrendingPosts, setPage, page, state}) {
  const trending = []
  
  function ChangePage(e){
    if(Number(e.target.attributes.value.value) === 1){
      if(TrendingPosts !== undefined){
        setPage(page+1)
      }else{
        setPage(1)
      }
    }else{
      if(TrendingPosts !== undefined){
        setPage(page-1)
      }else{
        setPage(0)
      }
    }
  }

  if(TrendingPosts !== undefined && TrendingPosts.detail !== 'Invalid token.'){
    for(let x=0; x<TrendingPosts.length; x++){
      let sliceP = 2
      let sliceN = 2
      if(TrendingPosts[x].positive.slice(2, 4)[0]==='0'){
        sliceP = 3
      }
      if(TrendingPosts[x].negative.slice(2, 4)[0]==='0'){
        sliceN = 3
      }
      trending.push(
        {value: TrendingPosts[x].value,
          pos_procent: TrendingPosts[x].positive.slice(sliceP, 4), neg_procent: TrendingPosts[x].negative.slice(sliceN, 4), social_icon: twitter_colored, 
          twitter_colored: twitter_colored, profile: TrendingPosts[x].name, 
          link: TrendingPosts[x].url, date: TrendingPosts[x].date, profile_icon: TrendingPosts[x].pic,
          text: TrendingPosts[x].text, icon: TrendingPosts[x].photo}
      )
    }
  }

  const trandings_posts = trending.map((elem, index) => {
  return(
      <div key={index}>
        <div className="progress_bars">
          <div className="positive" style={{width: elem.pos_procent + '%'}}>
            <span>{elem.pos_procent}%</span>
            <div className="bar"/>
          </div>
          <div className="negative" style={{width: elem.neg_procent + '%'}}>
            <span>{elem.neg_procent}%</span>
            <div className="bar"/>
          </div>
        </div>
        <div className="imgPost">
        <header>
            <div className="social-network">
              <img src={elem.social_icon} alt="social icon" />
            </div>
            <div className="profile_data">
              <div>
                <div>
                  <a href={elem.link}>{elem.profile}</a>
                </div>
                <div>
                  <span>{elem.date}</span>
                </div>
              </div>
              <div className='avatar'>
                <img src={elem.profile_icon} alt="profile icon" />
              </div>
            </div>
          </header>
          <div className="postText">
            <p>{elem.text}</p>
          </div>
          <img src={elem.icon} alt={elem.value} />
        </div>
      </div>
  )})
  return(
    <div className="posts_Div">
      <div className="posts">
        <div className="rank_header">
          <span className="topic">{state} Trending</span>
        </div>  
        <div className="post">      
          {trandings_posts}
        </div>      
      </div>
      <div className="arrows">
        <div><img src={left} alt="left-arrow" value="0" onClick={ChangePage} /></div>
        <div><img src={right} alt="right-arrow" value="1" onClick={ChangePage} /></div>
      </div>      
    </div>
  )
}
export default Trending_Post;