import React from 'react';
import './Profile.scss'
import Filters from "./Filters.js";
import { NavLink } from 'react-router-dom';

function Profile({name, filtersValue, setDatefilterStart, datefilterStart, setDatefilterStop, datefilterStop}) {
  let userData = JSON.parse(localStorage.getItem("userData"))

  let url = '5.187.6.228:8000' 

  let firstName
  let secondName 
  let pic 

  if(userData !== undefined){
    firstName = userData.first_name
    secondName = userData.second_name
    pic = "http://"+url+"/media/" + userData.pic
  }

  function logout(){
    localStorage.removeItem("userData")
    window.location.href = window.location.origin + '/'

  }

  return(     
    <div className="profile">
      <header>
        <div className="filters">
          <h1>{name}</h1>
          {filtersValue ?
          <Filters setDatefilterStart={setDatefilterStart} datefilterStart={datefilterStart} setDatefilterStop={setDatefilterStop} datefilterStop={datefilterStop} />
          : null}
        </div>
        <div className="user-field">
          <div className="notifications_search">
            <div className="logout">
              {/* <img alt="bell" src={bell} /> */}
              <button onClick={logout}>Logout</button>
            </div>
          </div>
          <div className="profile">
            {!pic? 
            <NavLink to='/' className="op_elem">Login</NavLink>
            : <div>
                <h6>{firstName} {secondName}</h6>
                <img alt="avatar" src={pic} />
              </div>
            }
          </div>
        </div>        
      </header>    
    </div>
  )
}

export default Profile;