  let [balanceDatefilterStop, setBalanceDatefilterStop] = useState('')

  let [municipalBalance, setMunicipalBalance] = useState()
  let [municipalBalanceModal, setMunicipalBalanceModal] = useState()
  let [balanceMap, setBalanceMap] = useState('')

  let [balanceModalState, setBalanceModalState] = useState(true)
  let [balanceDistrictNum, setBalanceDistrictNum] = useState(99)

  // variables for Education block 
