  let [educationChoosenSchools, setEducationChoosenSchools] = useState()
  let [educationChartSchool, setEducationChartSchool] = useState()
  let [educationSated, setEducationSated] = useState(false)
  let [educationYearFilter, setEducationYearFilter] = useState()
  let [educationTypeFilter, setEducationTypeFilter] = useState()

  let [educationYear, setEducationYear] = useState(2019)
  let [educationType, setEducationType] = useState('all')
  let [educationValueRequest, setEducationValueRequest] = useState('')

  // variables for migration block 
  let [migrationDatefilterStart, setMigrationDatefilterStart] = useState('')
  let [migrationDatefilterStop, setMigrationDatefilterStop] = useState('')

  let [migrationDiscrict, setmigrationDiscrict] = useState('Vračar')

  let [municipalMigration, setMunicipalMigration] = useState()

  let [url] = useState('5.187.6.228:8000') 

  let [userData] = useState(JSON.parse(localStorage.getItem("userData")))

  useEffect(() => {
    fetch(`http://`+url+`/api/municipal_insights/106_call_center/?start_date=`+callDatefilterStart.split('-').reverse().join('-')+`&end_date=`+callDatefilterStop.split('-').reverse().join('-'), {
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Token ${userData.token}`
      },
    })
    .then( data => data.json())
    .then( data => { setMunicipalCall(data) } )
    .catch( error => console.error(error))
  }, [url, userData, callDatefilterStart, callDatefilterStop])

  //Цвета и названия районов карты в блоке Shefa
