    greenCoin = euro
    redCoin = euro1
    blackCoin = euro3
    greyCoin = euro2
    mapValues = [
      {fill: '#6CE4A0', stroke: 'grey', subdivision: 'Stari Grad', city_score: '1%'},
      {fill: '#D86874', stroke: 'grey', subdivision: 'Savski Venac', city_score: '4%'},
      {fill: '#B3DF75', stroke: 'grey', subdivision: 'Vračar', city_score: '10%'},
      {fill: '#F3DA50', stroke: 'grey', subdivision: 'Novi Beograd', city_score: '1%'},
      {fill: '#6CE4A0', stroke: 'grey', subdivision: 'Čukarica', city_score: '7%'},
      {fill: '#EEB359', stroke: 'grey', subdivision: 'Rakovica', city_score: '1%'},
      {fill: '#6CE4A0', stroke: 'grey', subdivision: 'Voždovac', city_score: '9%'},
      {fill: '#6CE4A0', stroke: 'grey', subdivision: 'Zvezdara', city_score: '8%'},
      {fill: '#F3DA50', stroke: 'grey', subdivision: 'Zemun', city_score: '1%'},
      {fill: '#F3DA50', stroke: 'grey', subdivision: 'Palilula', city_score: '10%'},
      {fill: '#6CE4A0', stroke: 'grey', subdivision: 'Surčin', city_score: '6%'},
      {fill: '#D86874', stroke: 'grey', subdivision: 'Obrenovac', city_score: '9%'},
      {fill: '#F3DA50', stroke: 'grey', subdivision: 'Barajevo', city_score: '5%'},
      {fill: '#EEB359', stroke: 'grey', subdivision: 'Sopot', city_score: '1%'},
      {fill: '#F3DA50', stroke: 'grey', subdivision: 'Grocka', city_score: '3%'},
      {fill: '#EEB359', stroke: 'grey', subdivision: 'Lazarevac', city_score: '1%'},
      {fill: '#D86874', stroke: 'grey', subdivision: 'Mladenovac', city_score: '50%'}
    ] 
