import React from 'react'
import './Sidebar.scss';
import { NavLink } from 'react-router-dom';

import Overview from './../Static/Overview.svg'
import Insights from './../Static/Insights.png'
import SM from './../Static/SM.png'

function Sidebar() {
  let userData = JSON.parse(localStorage.getItem("userData"))

  const MunicipalInsightsSub = [
    { label: '106 Call Center', href: 'calls_center'},
    { label: 'Municipal Balance', href: 'municipal_balance'},
    { label: 'Education', href: 'education'},
    { label: 'Buisness Heat Map', href: ''},
    { label: 'Goverment GIS', href: 'goverment'},
    { label: 'Shefa', href: 'shefa'},
    { label: 'Immigration', href: 'immigration'}
  ]

  const MunicipalInsights = MunicipalInsightsSub.map((elem, index) =>{
    let hrf = '#'+elem.href 
    return(
      <div className="subMenu" key={index}>
        <a href={hrf}>{elem.label}</a>
     </div>
  )})
  
  const SmMonitoringSub = [
    { label: 'SM Stats', href: 'stats'},
    { label: 'Trending Posts', href: 'trending'},
    { label: 'Sentiment Analysis', href: 'chart'},
    { label: 'Trending Key Words', href: 'Trending-Topics'}
  ]

  const SmMonitoring = SmMonitoringSub.map((elem, index) =>{
    let hrf = '#'+elem.href 
    return(
      <div className="subMenu" key={index}>
        <a href={hrf}>{elem.label}</a>
     </div>
  )})
  const options = [
    { icon: Overview, label: 'Overview', href: '/', value: 0},
    { icon: Insights, label: 'Municipal Insights', subMenu: MunicipalInsights, href: 'municipal', value: 1},
    { icon: SM, label: 'SM Monitoring', subMenu: SmMonitoring, href: 'monitoring', value: 2},
  ]

  const sidebarOptions = options.map((option, index) =>{
    return(
      <div className="op" key={index}>
        <NavLink to={option.href} onClick={openMenu} value={option.value} className="op_elem">
          <img alt={option.label} src={option.icon} />
          <span>{option.label}</span>
        </NavLink>
        <div className="sub">
          {option.subMenu}
        </div>
     </div>
  )})
  function openMenu(e){
    let value = 0
    if(e.target.attributes.value === undefined){
      value = Number(e.target.parentNode.attributes.value.value)
    }else{
      value = Number(e.target.attributes.value.value)
    }
    let sub = document.querySelectorAll('.sub')
    let menuBool = true
    if(sub[value].style.display === 'flex'){
      menuBool = false
    }
    for(let x = 0; x < sub.length; x++){
      sub[x].style.display = 'none'
    }
    if(menuBool){
      sub[value].style.display = 'flex'
    }
  }
  return (
    <div className="Sidebar">
      <div className="Sidebar-upstair">
        <div className="Sidebar-Logo">
          <div className="Logo">
            <div className="half-circle"></div>
          </div>
          <h2>
            {userData.role === 1 ?
            "Belgrad"
            : 'Tel Aviv'}</h2>
        </div>
        <div className="options">
          {sidebarOptions}
        </div>
      </div>
    </div>
  );
}
  
export default Sidebar;