import React from 'react';
import './DonutBar.scss'

function Donut_Bar(props){
  // let rotateElem = 'rotate(' + 3.6*props.chartValues[index-1].procent.replace('%', '') + 'deg)'

  let values = props.chartValues
    const sections = values.map((elem, index) => {
      let id = 'section' + index
      let rotate = 'rotate(' + 3.6*elem.procent.replace('%', '') + 'deg)'
      let rotateElem = 'rotate(0deg)'
      let proc = 0
      if (index !== 0){
        for(let x = 0; x < index; x++){
          proc += Number(props.chartValues[x].procent.replace('%', ''))
          rotateElem = 'rotate(' + 3.6*proc + 'deg)'
        }
      }
      return(
        <div style={{ transform: String(rotateElem) }} key={index} id={id} className="clip">
          <div style={{ background: elem.color, transform: String(rotate) }} className="item" data-rel="21"></div>
        </div>
      )
    })
  return(
    <div className="donut-chart-block block">
      <div className="donut-chart">
         {sections}
        <div className="center"></div>
      </div>
    </div>
  )
}

export default Donut_Bar;